export const colorScheme = {
  back: "#1D1E7D",
  fore: "#F52277"
}

export default class Color {
  red = 0;
  green = 0;
  blue = 0;
  alpha = 1;

  constructor(color: string | number[], alpha?: number) {
    if (Array.isArray(color)) {
      this.red = color[0];
      this.green = color[1];
      this.blue = color[2];
      if (color.length > 3) this.alpha = color[3];
    } else if (color.startsWith("rgb")) {
      let w = color.substring(color.indexOf("(") + 1 ,color.indexOf(")")).split(",").map(item => parseInt(item));
      this.red = w[0];
      this.green = w[1];
      this.blue = w[2];
      if (w.length > 3) this.alpha = w[3];
    } else {
      let w = color.replace("#", "");
      this.red = parseInt('0x' + w.slice(0, 2));
      this.green = parseInt('0x' + w.slice(2, 4));
      this.blue = parseInt('0x' + w.slice(4, 6));
    }
    if (alpha) this.alpha = alpha;
  }

  get hex() {
    let hexStr = "#" + ((1 << 24) + (this.red << 16) + (this.green << 8) + this.blue).toString(16).slice(1);
    return hexStr;
  }

  get rgbStr() {
    return `rgb(${this.red}, ${this.green}, ${this.blue})`
  }

  get rgbaStr() {
    return `rgba(${this.red}, ${this.green}, ${this.blue}, ${this.alpha})`
  }

  get rgbArray() {
    return [this.red, this.green, this.blue]
  }
}

function getColorDistance(colorA: Color, colorB: Color) {
  let colorARGB = colorA.rgbArray, colorBRGB = colorB.rgbArray;
  let distance = 0;

  for (let n = 0; n < colorARGB.length; n++) {
    distance += Math.pow(colorARGB[n] - colorBRGB[n], 2);
  }

  return Math.sqrt(distance);
}

function getColorDistancesOfArray(colors: Color[]) {
  type distance = distanceChild[];
  type distanceChild = number[];

  let distances: distance = [];

  for (let c1 = 0; c1 < colors.length; c1++) {
    distances[c1] = [];
    for (let c2 = 0; c2 < colors.length; c2++) {
      distances[c1][c2] = getColorDistance(colors[c1], colors[c2]);
    }
  }

  return distances;
}

export function selectThreeColors(colors: Color[]) {
  let colorDistances = getColorDistancesOfArray(colors);
  let color2Index = 0, color3Index = 1;

  let color1 = colors[0];

  for (let i = 0; i < colors.length; i++) {
    if (colorDistances[0][i] > colorDistances[0][color2Index]) {
      color2Index = i;
    }
  }

  let color2 = colors[color2Index];
  color3Index = color2Index + 1;
  if (color3Index >= colors.length) {
    color3Index = 1;
  }

  for (let i = 1; i < colors.length; i++) {
    if (i !== color2Index && colorDistances[0][i] > colorDistances[0][color3Index]) {
      color3Index = i;
    }
  }

  let color3 = colors[color3Index];

  return [color1, color2, color3];
}

/**
 * return "rgba(0, 0, 0, alpha)"
 * @param alpha 
 */

export function dcr(alpha: number) {
  let color = new Color("#000000", alpha);
  return color.rgbaStr;
}

/**
 * return "rgba(255, 255, 255, alpha)"
 * @param alpha 
 */

export function lcr(alpha: number) {
  let color = new Color("#FFFFFF", alpha);
  return color.rgbaStr;
}

export function acr(alpha: number, hex: string) {
  let color = new Color(hex, alpha);
  return color.rgbaStr;
}