import AV from 'leancloud-storage';

AV.init({
  appId: "xKR2tyyilhkTVKN0m228niWM-MdYXbMMI",
  appKey: "QwHrti9HBOiY8vJM62lAA1xR"
});

const LikeCount = AV.Object.extend("likeCount");
const likeDemo = new LikeCount();

export function getLikeCount(url: string) {
  const getCounter = new AV.Query("likeCount");
  const getCount = getCounter.equalTo("url", url);

  return getCount.find()
    .then(results => {
      const count: number = results.length >= 1 ? (results[0] as any).attributes.num : 0;
      return count;
    })
    .catch(error => {
      console.error(error);
      return null;
    })
}

export function addLikeCount(url: string) {
  const getCounter = new AV.Query("likeCount");
  const getCount = getCounter.equalTo("url", url);

  getCount.find().then(results => {
    if (results.length >= 1) {  //成功返回数据（已创建对象，更新 num 数据）
      let countNUM = (results[0] as any).attributes.num;  //获取云端计数
      let objId = results[0].id;  //获取当前页面 id
      countNUM++;  //本地计数++

      const increase = AV.Object.createWithoutData('likeCount', objId);  //获取当前页面 id 的云端对象
      increase.increment('num', 1);  //使用 increment 函数更新云端对象的 num 属性
      //储存（更新）对象到云端
      increase.save().then(increase => {
        // console.log("Update Successfully.");
      }, error => {
        console.log("Update Error.")
      })
    } else {  //返回空数据（未创建对象，第一次创建num+url）
      let countNUM = 0;  //初始化本地计数
      let countURL = url;  //获取当前 url
      countNUM++;  //本地计数++
      //储存 num（本地计数） 和 url（当前url） 对象到云端
      likeDemo.save({
        'num': countNUM,
        'url': countURL
      }).then(result => {
        // console.log("Save Successfully.");
      }, error => {
        console.log("Save Error.")
      })
    }
  })
}

export function getCommentCount(url: string) {
  const getCounter = new AV.Query("Comment");
  const getCount = getCounter.equalTo("url", url);

  return getCount.count()
    .then(results => {
      return results;
    })
    .catch(error => {
      console.error(error);
      return null;
    })
}