import { CSSProperties, useEffect, useState } from "react";
import { addLikeCount, getLikeCount } from "../lib/like";
import IconBtn from "./icon-btn";

interface LikeBtnProps {
  path: string;
  style?: CSSProperties;
}

export default function LikeBtn({ path, style }: LikeBtnProps) {
  const [likeCount, setLikeCount] = useState<number | null>(null);
  const [liked, setLiked] = useState(false);

  useEffect(() => {
    getLikeCount(path).then(count => {
      setLikeCount(count);
    });
  }, [])

  return(
    <IconBtn title={likeCount? likeCount.toString() : "Like"} style={style} icon={liked? 0xe601 : 0xe648} onClick={likeClick} />
  )

  function likeClick() {
    if (!liked) {
      addLikeCount(path);
      setLiked(true);
      setLikeCount(prevLike => {
        if (prevLike === null) {
          return 1;
        } else {
          return prevLike + 1;
        }
      });
    }
  }
}