import { css } from "@emotion/react"
import { colorScheme, dcr } from "../lib/color"
import { Location } from '@reach/router';
import { Link } from "gatsby";

export default function Nav() {
  return(
    <Location>
      {locationProps => <NavInner {...locationProps} />}
    </Location>
  )
}

function NavInner({ location }) {
  const pathname = location.pathname;

  return(
    <nav css={styles.nav}>
      <NavItem icon="&#xe64d;" to="/" select={pathname === "/"} />
      <NavItem icon="&#xe64a;" to="#" />
      <NavItem icon="&#xe63d;" to="#" />
    </nav>
  )
}

interface NavItemProps {
  icon: string;
  to: string;
  select?: boolean;
}

function NavItem({ icon, to, select }: NavItemProps) {
  return(
    <Link
      to={to}
      css={[styles.navItem, css`
        opacity: ${select? 1 : 0.3};
      `]}
    >
      {icon}
    </Link>
  )
}

const styles = {
  nav: css`
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    transform: translateZ(0);
    top: 30px;
    width: 50px;
    padding: 10px 0;
    background: ${dcr(0.05)};
    border-radius: 8px;
  `,
  navItem: css`
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    width: 100%;
    height: 55px;
    color: inherit;
    user-select: none;
    text-decoration: none;
    font-family: 'iconfont';
    transition: 200ms;
    :hover {
      opacity: 1;
    }
    :active {
      transform: scale(0.9);
    }
  `
}