import { CSSProperties, useEffect, useState } from "react";
import { getCommentCount } from "../lib/like";
import IconBtn from "./icon-btn";

interface CommentBtnProps {
  path: string;
  style?: CSSProperties;
}

export default function CommentBtn({ path, style }: CommentBtnProps) {
  const [commentCount, setCommentCount] = useState<number | null>(null);

  useEffect(() => {
    getCommentCount(path).then(count => {
      setCommentCount(count);
    });
  }, [])

  return(
    <IconBtn title={commentCount? commentCount.toString() : "Comment"} style={style} icon="&#xe641;" />
  )
}