import { css, Theme } from "@emotion/react";
import { Interpolation } from '@emotion/serialize';
import { CSSProperties, MouseEvent } from "react";
import { colorScheme, lcr } from "../lib/color";

interface IconBtnProps {
  title: string;
  icon?: string | number;
  style?: CSSProperties;
  onClick?: (event: MouseEvent<HTMLButtonElement, globalThis.MouseEvent>) => void;
}

export default function IconBtn({ title, icon, style, onClick }: IconBtnProps) {
  return(
    <button css={styles.btn} style={style} onClick={onClick}>
      {icon? <div css={styles.icon}>{
        typeof icon === 'number'? String.fromCharCode(icon) : icon
      }</div> : null}
      {title}
    </button>
  )
}

const styles = {
  btn: css`
    display: flex;
    align-items: center;
    border: none;
    background: ${lcr(0.1)};
    color: ${colorScheme.fore};
    padding: 0 15px;
    height: 35px;
    border-radius: 20px;
    outline: none;
    font-size: 15px;
    font-weight: bold;
    transition: 100ms;
    cursor: pointer;
    :hover {
      background: ${lcr(0.2)};
    }
    :active {
      background: ${lcr(0.05)};
    }
  `,
  icon: css`
    font-family: 'iconfont';
    font-size: 20px;
    margin-right: 8px;
  `
}